import guides from '~/contents/guides/guides.js'

export default {
  components: {
  },
  async asyncData ({ route }) {
    const promises = guides.map(guide => import(`~/contents/guides/${guide}.md`))
    return { guides: await Promise.all(promises) }
  },
  head() {
    return {
      title: "Portfolio"
    }
  }
}
